html, body {
  width: 100%;
  overflow-x: hidden;
  position: relative;
  -webkit-text-size-adjust: 100%;
  word-break: keep-all;
  background-color: #fff;
  font-family: 'Inter', 'Noto Sans KR', sans-serif !important;
  height: 100%;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a:hover {
  text-decoration: none;
}

button {
  background-color: transparent;
  border: 0;
}

button:focus, button:active {
  outline: none;
}

#wrap {
  overflow-x: hidden;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

#container {
  flex: auto 1;
}

* {
  margin: 0;
  padding: 0;
}
